/** Member-to-member block. */
export interface MemberToMemberBlock {
  /**
   * ID of the member that is the creator of this block.
   * @format GUID
   */
  blockingMemberId?: string;
  /**
   * ID of the member that is blocked by this block.
   * @format GUID
   */
  blockedMemberId?: string;
}

export interface BlockMemberRequest {
  /**
   * ID of a member to block.
   * @format GUID
   */
  memberId: string;
}

export interface BlockMemberResponse {}

export interface MemberBlockedByMember {
  /** The block that has been created. */
  block?: MemberToMemberBlock;
}

export interface MemberAlreadyBlockedError {
  /** @format GUID */
  initiatorMemberId?: string;
  /** @format GUID */
  targetMemberId?: string;
}

export interface SelfBlockingForbiddenError {
  /** @format GUID */
  initiatorMemberId?: string;
}

export interface AdminBlockingForbiddenError {
  /** @format GUID */
  initiatorMemberId?: string;
  /** @format GUID */
  targetMemberId?: string;
  /** @format GUID */
  adminMemberId?: string;
}

export interface UnblockMemberRequest {
  /**
   * ID of a member to unblock.
   * @format GUID
   */
  memberId: string;
}

export interface UnblockMemberResponse {}

export interface MemberUnblockedByMember {
  /** The block that has been removed. */
  block?: MemberToMemberBlock;
}

export interface SelfUnblockingForbiddenError {
  /** @format GUID */
  initiatorMemberId?: string;
}

export interface BlockDoesNotExistError {
  /** @format GUID */
  blockingMemberId?: string;
  /** @format GUID */
  blockedMemberId?: string;
}

export interface ListCurrentMemberBlockingRequest {
  /** Pagination parameters. */
  cursorPaging?: CursorPaging;
}

export interface CursorPaging {
  /**
   * Maximum number of items to return in the results.
   * @max 100
   */
  limit?: number | null;
  /**
   * Pointer to the next or previous page in the list of results.
   *
   * Pass the relevant cursor token from the `pagingMetadata` object in the previous call's response.
   * Not relevant for the first request.
   * @maxLength 16000
   */
  cursor?: string | null;
}

export interface ListCurrentMemberBlockingResponse {
  /**
   * IDs of members blocked by the current member.
   * @format GUID
   */
  blockedMemberIds?: string[];
  /** Metadata for the paginated results. */
  pagingMetadata?: PagingMetadataV2;
}

export interface PagingMetadataV2 {
  /** Number of items returned in the response. */
  count?: number | null;
  /** Offset that was requested. */
  offset?: number | null;
  /** Total number of items that match the query. Returned if offset paging is used and the `tooManyToCount` flag is not set. */
  total?: number | null;
  /** Flag that indicates the server failed to calculate the `total` field. */
  tooManyToCount?: boolean | null;
  /** Cursors to navigate through the result pages using `next` and `prev`. Returned if cursor paging is used. */
  cursors?: Cursors;
}

export interface Cursors {
  /**
   * Cursor string pointing to the next page in the list of results.
   * @maxLength 16000
   */
  next?: string | null;
  /**
   * Cursor pointing to the previous page in the list of results.
   * @maxLength 16000
   */
  prev?: string | null;
}

export interface ListMemberBlocksRequest {
  /**
   * ID of a member whose blocks are requested.
   * @format GUID
   */
  memberId: string;
  /** Pagination parameters. */
  cursorPaging?: CursorPaging;
}

export interface ListMemberBlocksResponse {
  /** List of members who are either blocking or blocked by the specified member. */
  memberBlocks?: MemberBlock[];
  /** Metadata for the paginated results. */
  pagingMetadata?: PagingMetadataV2;
}

export interface MemberBlock {
  /**
   * Member ID.
   * @format GUID
   */
  memberId?: string;
  /**
   * Indicates whether the member with `memberId` is blocking or blocked by the specified member.
   *
   * - `BLOCKING`: The member with `memberId` is blocking the requested member.
   * - `BLOCKED`: The member with `memberId` is blocked by the requested member.
   */
  blockDirection?: BlockDirectionWithLiterals;
}

export enum BlockDirection {
  UNKNOWN = 'UNKNOWN',
  BLOCKING = 'BLOCKING',
  BLOCKED = 'BLOCKED',
}

/** @enumType */
export type BlockDirectionWithLiterals =
  | BlockDirection
  | 'UNKNOWN'
  | 'BLOCKING'
  | 'BLOCKED';

export interface DomainEvent extends DomainEventBodyOneOf {
  createdEvent?: EntityCreatedEvent;
  updatedEvent?: EntityUpdatedEvent;
  deletedEvent?: EntityDeletedEvent;
  actionEvent?: ActionEvent;
  /** Event ID. With this ID you can easily spot duplicated events and ignore them. */
  id?: string;
  /**
   * Fully Qualified Domain Name of an entity. This is a unique identifier assigned to the API main business entities.
   * For example, `wix.stores.catalog.product`, `wix.bookings.session`, `wix.payments.transaction`.
   */
  entityFqdn?: string;
  /**
   * Event action name, placed at the top level to make it easier for users to dispatch messages.
   * For example: `created`/`updated`/`deleted`/`started`/`completed`/`email_opened`.
   */
  slug?: string;
  /** ID of the entity associated with the event. */
  entityId?: string;
  /** Event timestamp in [ISO-8601](https://en.wikipedia.org/wiki/ISO_8601) format and UTC time. For example, `2020-04-26T13:57:50.699Z`. */
  eventTime?: Date | null;
  /**
   * Whether the event was triggered as a result of a privacy regulation application
   * (for example, GDPR).
   */
  triggeredByAnonymizeRequest?: boolean | null;
  /** If present, indicates the action that triggered the event. */
  originatedFrom?: string | null;
  /**
   * A sequence number that indicates the order of updates to an entity. For example, if an entity was updated at 16:00 and then again at 16:01, the second update will always have a higher sequence number.
   * You can use this number to make sure you're handling updates in the right order. Just save the latest sequence number on your end and compare it to the one in each new message. If the new message has an older (lower) number, you can safely ignore it.
   */
  entityEventSequence?: string | null;
}

/** @oneof */
export interface DomainEventBodyOneOf {
  createdEvent?: EntityCreatedEvent;
  updatedEvent?: EntityUpdatedEvent;
  deletedEvent?: EntityDeletedEvent;
  actionEvent?: ActionEvent;
}

export interface EntityCreatedEvent {
  entityAsJson?: string;
  /** Indicates the event was triggered by a restore-from-trashbin operation for a previously deleted entity */
  restoreInfo?: RestoreInfo;
}

export interface RestoreInfo {
  deletedDate?: Date | null;
}

export interface EntityUpdatedEvent {
  /**
   * Since platformized APIs only expose PATCH and not PUT we can't assume that the fields sent from the client are the actual diff.
   * This means that to generate a list of changed fields (as opposed to sent fields) one needs to traverse both objects.
   * We don't want to impose this on all developers and so we leave this traversal to the notification recipients which need it.
   */
  currentEntityAsJson?: string;
}

export interface EntityDeletedEvent {
  /** Entity that was deleted. */
  deletedEntityAsJson?: string | null;
}

export interface ActionEvent {
  bodyAsJson?: string;
}

export interface Empty {}

export interface MessageEnvelope {
  /**
   * App instance ID.
   * @format GUID
   */
  instanceId?: string | null;
  /**
   * Event type.
   * @maxLength 150
   */
  eventType?: string;
  /** The identification type and identity data. */
  identity?: IdentificationData;
  /** Stringify payload. */
  data?: string;
}

export interface IdentificationData extends IdentificationDataIdOneOf {
  /**
   * ID of a site visitor that has not logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor that has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user (site owner, contributor, etc.).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
  /** @readonly */
  identityType?: WebhookIdentityTypeWithLiterals;
}

/** @oneof */
export interface IdentificationDataIdOneOf {
  /**
   * ID of a site visitor that has not logged in to the site.
   * @format GUID
   */
  anonymousVisitorId?: string;
  /**
   * ID of a site visitor that has logged in to the site.
   * @format GUID
   */
  memberId?: string;
  /**
   * ID of a Wix user (site owner, contributor, etc.).
   * @format GUID
   */
  wixUserId?: string;
  /**
   * ID of an app.
   * @format GUID
   */
  appId?: string;
}

export enum WebhookIdentityType {
  UNKNOWN = 'UNKNOWN',
  ANONYMOUS_VISITOR = 'ANONYMOUS_VISITOR',
  MEMBER = 'MEMBER',
  WIX_USER = 'WIX_USER',
  APP = 'APP',
}

/** @enumType */
export type WebhookIdentityTypeWithLiterals =
  | WebhookIdentityType
  | 'UNKNOWN'
  | 'ANONYMOUS_VISITOR'
  | 'MEMBER'
  | 'WIX_USER'
  | 'APP';
/** @docsIgnore */
export type BlockMemberApplicationErrors =
  | {
      code?: 'MEMBER_ALREADY_BLOCKED';
      description?: string;
      data?: MemberAlreadyBlockedError;
    }
  | {
      code?: 'SELF_BLOCKING_FORBIDDEN';
      description?: string;
      data?: SelfBlockingForbiddenError;
    }
  | {
      code?: 'ADMIN_BLOCKING_FORBIDDEN';
      description?: string;
      data?: AdminBlockingForbiddenError;
    };
/** @docsIgnore */
export type UnblockMemberApplicationErrors =
  | {
      code?: 'SELF_UNBLOCKING_FORBIDDEN';
      description?: string;
      data?: SelfUnblockingForbiddenError;
    }
  | {
      code?: 'BLOCK_DOES_NOT_EXIST';
      description?: string;
      data?: BlockDoesNotExistError;
    };
